import { useEffect, useState } from 'react';
import MarkdownPreview from '@uiw/react-markdown-preview';
import Code from './Code';
import pkg from '@uiw/react-layout/package.json';
import styles from './index.module.less';

export interface CreatePageProps<T> extends React.HTMLAttributes<HTMLDivElement> {
  dependencies?: T;
  path: string;
  renderPage?: () => Promise<string>;
}

const getCodeStr = (data: any[] = [], code: string = '') => {
  data.forEach((node) => {
    if (node.type === 'text') {
      code += node.value;
    } else if (node.children && Array.isArray(node.children)) {
      code += getCodeStr(node.children);
    }
  });
  return code;
};

export default function CreatePage<T>(props: CreatePageProps<T>) {
  const { renderPage, dependencies, path, ...other } = props;
  const [mdStr, setMdStr] = useState('');
  useEffect(() => {
    if (renderPage) {
      renderPage()
        .then((str) => {
          setMdStr(str);
        })
        .catch(() => {});
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (!mdStr) {
    return null;
  }

  return (
    <div {...other}>
      <MarkdownPreview
        source={mdStr}
        className={styles.markdown}
        components={{
          /**
           * bordered 
           * bgWhite 
           * noCode 
           * noPreview 
           * noScroll 
           * codePen  Codepen   import  Codepen 
           */
          code: ({ inline, node, ...props }) => {
            const { noPreview, bordered, noScroll, bgWhite, noCode, codePen, codeSandbox } = props as any;
            if (inline) {
              return <code {...props} />;
            }
            const config = {
              noPreview,
              bordered,
              noScroll,
              bgWhite,
              noCode,
              codePen,
              codeSandbox,
            } as any;
            if (Object.keys(config).filter((name) => config[name] !== undefined).length === 0) {
              return <code {...props} />;
            }
            return (
              <Code
                version={pkg.version}
                code={getCodeStr(node.children)}
                dependencies={dependencies}
                {...{ noPreview, bordered, noScroll, bgWhite, noCode, codePen, codeSandbox }}
              />
            );
          },
        }}
      />
    </div>
  );
}
